/* src/app/components/footer/footer-compenent.scss */
.app-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 48px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 8px;
  padding: 0 8px;
  background: #121212;
  color: #fff;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
  z-index: 1000;
  box-shadow: 0 -1px 6px rgba(0, 0, 0, 0.6);
}
.app-footer,
.app-footer *,
.app-footer button,
.app-footer .mat-button,
.app-footer .mat-button .mat-button-wrapper {
  color: #fff !important;
}
.app-footer button.mat-button {
  min-width: 40px;
  height: 36px;
  padding: 4px 6px;
  border-radius: 6px;
  color: #fff !important;
  background: transparent !important;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}
.app-footer button.mat-button .mat-icon {
  font-size: 20px;
  line-height: 20px;
  margin: 0;
  color: #fff !important;
  fill: #fff !important;
}
.app-footer button.mat-button span {
  display: none;
  color: #fff !important;
}
.app-footer button.mat-button:hover {
  background: rgba(255, 255, 255, 0.04) !important;
}
.app-footer button.mat-button:active {
  background: rgba(255, 255, 255, 0.06) !important;
}
@media (min-width: 900px) {
  .app-footer {
    height: 56px;
    gap: 12px;
  }
  .app-footer button.mat-button span {
    display: inline-block;
    margin-left: 6px;
    font-size: 13px;
    color: #fff !important;
  }
}
/*# sourceMappingURL=footer-compenent.css.map */
