/* src/app/components/chart/chart-component.scss */
.trading-view-container {
  display: flex;
  flex-direction: column;
  min-height: calc(100svh - var(--footer-height));
  height: calc(100svh - var(--footer-height));
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    "Trebuchet MS",
    Roboto,
    Ubuntu,
    sans-serif;
  overflow: hidden;
  padding: 0;
  box-sizing: border-box;
  padding-bottom: env(safe-area-inset-bottom, 0);
}
@supports not (height: 100svh) {
  .trading-view-container {
    min-height: calc(100dvh - var(--footer-height));
    height: calc(100dvh - var(--footer-height));
  }
}
:host {
  display: block;
  height: 100%;
  max-height: 100dvh;
  overflow: hidden;
}
.trading-view-container,
.chart-container,
.chart-area {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.symbol-select,
.timeframe-select {
  --mat-select-arrow-transform: none;
  font-size: 13px;
}
.symbol-select {
  max-width: 180px;
}
.timeframe-select-short {
  --mat-select-arrow-transform: none;
  max-width: 90px;
  font-size: 13px;
}
.chart-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 0;
  margin: 0;
}
.price-info-bar {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid var(--mat-divider-color);
  position: relative;
  height: 61px !important;
  margin-left: 8px;
}
.price-info-bar .top-controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.price-info-bar .top-controls .select-group {
  display: flex;
  align-items: center;
  gap: 8px;
}
.price-info-bar .top-controls mat-form-field {
  display: inline-flex !important;
  width: auto !important;
}
.price-info-bar .top-controls .top-settings {
  flex-shrink: 0;
  margin-left: auto;
  margin-top: -25px;
  margin-right: 8px;
}
.price-info-bar .top-controls .settings-btn-top {
  background: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4px;
  height: 40px;
  width: 40px;
  transition: color 0.2s ease, transform 0.2s ease;
}
.price-info-bar .top-controls .settings-btn-top:hover {
  transform: scale(1.1);
}
.settings-panel-overlay {
  position: absolute;
  top: 56px;
  right: 16px;
  min-width: 240px;
  border-radius: 10px;
  padding: 16px 18px;
  box-shadow: 0 4px 24px 0 rgba(0, 0, 0, 0.45);
  border: 1px solid #222;
  background: #181a20;
  color: #eee;
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 15px;
  animation: fadeInPanel 0.18s ease;
}
@keyframes fadeInPanel {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.settings-panel-overlay label span {
  color: #eee !important;
}
.settings-panel-overlay input[type=checkbox],
.settings-panel-overlay input[type=radio] {
  accent-color: #fff;
  background: #222;
  border-radius: 4px;
}
.settings-panel-overlay input[type=checkbox]:checked,
.settings-panel-overlay input[type=radio]:checked {
  accent-color: #00bcd4;
}
.chart-container {
  flex: 1;
  position: relative;
  min-height: 0;
  touch-action: none;
  -webkit-user-select: none;
  user-select: none;
  cursor: grab;
  margin: 0;
  overflow: hidden;
  max-height: none;
  box-sizing: border-box;
}
.chart-container.interacting {
  cursor: grabbing;
}
.chart-container canvas {
  width: 100% !important;
  height: 100% !important;
  display: block;
}
.chart-container .axis-zones {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 1;
}
.chart-toolbar {
  margin: 0;
  padding: 0 12px;
  box-shadow: none;
  border: none;
}
@media (max-width: 480px) {
  .chart-container {
    max-height: none !important;
    padding-bottom: 10px;
  }
  .settings-panel-overlay {
    left: 8px;
    top: 64px;
    right: 8px;
    min-width: auto;
  }
  .price-info-bar {
    padding: 8px 12px;
  }
  .symbol-selector .symbol-dropdown {
    min-width: 80px;
    font-size: 13px;
  }
  .timeframe-selector .timeframe-btn {
    padding: 4px 6px;
    font-size: 11px;
  }
}
.symbol-select.mat-form-field,
.timeframe-select.mat-form-field {
  max-width: 110px;
  height: 32px;
}
.symbol-select.mat-form-field .mat-mdc-form-field-flex,
.timeframe-select.mat-form-field .mat-mdc-form-field-flex {
  height: 32px;
  min-height: 32px;
  padding: 0 6px;
}
.symbol-select.mat-form-field .mat-mdc-select-value,
.symbol-select.mat-form-field .mat-mdc-select-placeholder,
.timeframe-select.mat-form-field .mat-mdc-select-value,
.timeframe-select.mat-form-field .mat-mdc-select-placeholder {
  font-size: 13px;
  line-height: 32px;
}
.symbol-select.mat-form-field .mat-mdc-form-field-infix,
.timeframe-select.mat-form-field .mat-mdc-form-field-infix {
  padding: 0 !important;
  min-height: 32px !important;
}
.symbol-select.mat-form-field .mat-mdc-form-field-outline,
.timeframe-select.mat-form-field .mat-mdc-form-field-outline {
  border-radius: 6px;
}
.symbol-select.mat-form-field .mat-mdc-form-field-outline-start,
.symbol-select.mat-form-field .mat-mdc-form-field-outline-end,
.timeframe-select.mat-form-field .mat-mdc-form-field-outline-start,
.timeframe-select.mat-form-field .mat-mdc-form-field-outline-end {
  height: 32px;
}
.symbol-select.mat-form-field .mat-mdc-select-trigger,
.timeframe-select.mat-form-field .mat-mdc-select-trigger {
  height: 32px;
  display: flex;
  align-items: center;
}
.symbol-option {
  display: inline-flex;
  align-items: center;
  gap: 6px;
}
.symbol-icon {
  width: 18px;
  height: 18px;
  object-fit: contain;
  filter: drop-shadow(0 0 2px rgba(0, 0, 0, 0.4));
}
.symbol-fallback {
  font-size: 18px !important;
  line-height: 18px !important;
  width: 18px;
  height: 18px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #8ab4f8;
}
.mat-mdc-select-panel .symbol-option .symbol-text {
  font-size: 13px;
}
@media (orientation: landscape) and (max-width: 900px) {
  .chart-container {
    flex-direction: row;
    padding-bottom: 0;
    padding-left: 10px;
    padding-right: 10px;
    min-height: 0;
    height: 100%;
    max-height: 100vh;
  }
  .price-info-bar {
    flex-direction: row;
    align-items: center;
    height: 48px !important;
    margin-left: 0;
    padding: 4px 10px;
  }
  .settings-panel-overlay {
    top: 48px;
    right: 10px;
    left: auto;
    min-width: 200px;
    font-size: 14px;
  }
  .symbol-select {
    max-width: 120px;
    font-size: 12px;
  }
  .timeframe-select-short {
    max-width: 70px;
    font-size: 12px;
  }
  .chart-toolbar {
    padding: 0 6px;
  }
  .symbol-selector .symbol-dropdown {
    min-width: 60px;
    font-size: 12px;
  }
  .timeframe-selector .timeframe-btn {
    padding: 2px 4px;
    font-size: 10px;
  }
}
/*# sourceMappingURL=chart-component.css.map */
