/* src/app/components/account-balance/account-balance.component.scss */
.account-balance-page {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.summary .grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap: 0.75rem;
}
.summary .metric {
  background: #1e1e1e;
  padding: 0.5rem 0.75rem;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  font-size: 12px;
}
.summary .metric label {
  font-weight: 600;
  opacity: 0.8;
}
.summary .metric span {
  font-size: 14px;
}
.positive {
  color: #1fa672;
}
.negative {
  color: #ff4d4f;
}
.neutral {
  color: #ccc;
}
table {
  width: 100%;
  font-size: 12px;
}
th {
  text-align: left;
}
td.positive {
  font-weight: 600;
}
td.negative {
  font-weight: 600;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}
.loader {
  display: flex;
  justify-content: center;
  padding: 2rem;
}
.error {
  color: #ff4d4f;
  font-weight: 600;
}
/*# sourceMappingURL=account-balance.component.css.map */
