/* src/app/components/login/login.component.scss */
:host {
  display: flex;
  justify-content: center;
  align-content: center;
  overflow: hidden;
  height: 100dvh;
  background-color: var(--sys-background);
}
:host mat-card {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--sys-outline);
  margin: auto;
  opacity: 98%;
  box-shadow: 0 0 8px 0 var(--sys-shadow);
  background-color: var(--sys-surface-container-high);
  color: var(--sys-on-surface);
}
:host mat-card mat-card-content {
  display: block;
}
:host mat-card mat-card-content mat-form-field {
  width: 100%;
  padding-bottom: 16px;
  color: var(--sys-on-surface);
}
:host mat-card mat-card-content button {
  width: 100%;
  background-color: var(--sys-primary);
  color: var(--sys-on-primary);
  font-weight: bold;
}
:host mat-card mat-card-content button:hover {
  background-color: var(--sys-primary-fixed-dim);
}
:host .loading-overlay {
  display: flex;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: var(--sys-surface-container-highest);
  opacity: 0.7;
  border-radius: 12px;
  z-index: 1;
}
:host .loading-overlay mat-spinner {
  margin: auto;
}
